import photo from "../assets/dev.jpeg";
import cv from "../assets/cv.pdf";
import { AiFillGithub, AiFillLinkedin } from "react-icons/ai";

import { useNavigate } from "react-router-dom";

export const Home = () => {
  const navigate = useNavigate();

  return (
    <div className="home__container">
      <div className="home__image-container">
        <img src={photo} alt="photo-dev" />
      </div>
      <div className="home_text-container">
        <div className="home__social-container">
          <a
            className="social-link"
            href="https://github.com/troyanito123"
            target="_blank"
          >
            <AiFillGithub size={32} />
          </a>
          <a
            className="social-link"
            href="https://www.linkedin.com/in/jassmanni-quinones-alvarez"
            target="_blank"
          >
            <AiFillLinkedin size={32} />
          </a>
        </div>

        <div className="home__name">
          <p>Hello, I'M JAZZ</p>
        </div>

        <div className="home__resume-container">
          <p>Full Stack Developer</p>
          <p>(continuous learning, continuous improvement) {"=>"} SUCCESS!</p>
        </div>

        <div className="home_action-container">
          <button
            className="btn btn-outline"
            onClick={() => navigate("/contact")}
          >
            Contact me
          </button>
          <a className="btn btn-primary" href={cv} download="resume.pdf">
            Get resume
          </a>
        </div>
      </div>
    </div>
  );
};
