import image1 from "../assets/01.webp";
import image2 from "../assets/02.webp";
import image3 from "../assets/03.webp";
import imagew1 from "../assets/w01.JPG";
import imagew2 from "../assets/w02.JPG";
import imagew3 from "../assets/w03.JPG";
import { AiFillGithub } from "react-icons/ai";
import { IoLogoGooglePlaystore } from "react-icons/io5";

import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from "react-responsive-carousel";

export const Proyect = () => {
  return (
    <div className="proyect">
      <div className="proyect__title-container">
        <h1 className="page-title">My proyects</h1>
        <p className="page-subtitle">My own proyect as freelancer</p>
      </div>

      <div className="proyect__card shadow-small">
        <div className="proyect__card-title">
          <h2>Movil App </h2>
          <p>
            Mobile application for administration, sale and delivery of liquor
          </p>
          <p>Develop with IONIC - FIREBASE</p>
        </div>
        <div className="proyect__card-images-container">
          <Carousel showThumbs={false} infiniteLoop autoPlay stopOnHover>
            <div className="carrusel-image ">
              <img src={image1} />
            </div>

            <div className="carrusel-image ">
              <img src={image2} />
            </div>
            <div className="carrusel-image ">
              <img src={image3} />
            </div>
          </Carousel>
        </div>
        <div className="proyect__card-footer">
          <AiFillGithub color="#247881" size={32} />
          <p>
            <a
              href="https://github.com/mauriciofp/LicoreriaApp"
              target="_blank"
            >
              View code on GITHUB
            </a>
          </p>
        </div>
        <div className="proyect__card-footer">
          <IoLogoGooglePlaystore color="#247881" size={32} />
          <p>
            <a
              href="https://play.google.com/store/apps/details?id=umss.ptaang.licorcito"
              target="_blank"
            >
              Link to play store
            </a>
          </p>
        </div>
      </div>
      <div className="proyect__card shadow-small">
        <div className="proyect__card-title">
          <h2>Wep App </h2>
          <p>Administrative dashboard for a neighborhood</p>
          <p>Develop with ANGULAR - NESTJS - POSTGRESQL</p>
        </div>
        <div className="proyect__card-images-container">
          <Carousel showThumbs={false} infiniteLoop autoPlay stopOnHover>
            <div className="carrusel-image ">
              <img src={imagew1} />
            </div>

            <div className="carrusel-image ">
              <img src={imagew2} />
            </div>
            <div className="carrusel-image ">
              <img src={imagew3} />
            </div>
          </Carousel>
        </div>
        <div className="proyect__card-footer">
          <AiFillGithub color="#247881" size={32} />
          <p>
            <a href="https://github.com/troyanito123/otb-web" target="_blank">
              View code on GITHUB
            </a>
          </p>
        </div>
      </div>
    </div>
  );
};
