import { useNavigate } from "react-router-dom";
import about_image from "../assets/about_image.svg";
import cv from "../assets/cv.pdf";

export const About = () => {
  const navigate = useNavigate();
  const stacks = [
    "ANGULAR - IONIC",
    "NODEJS (NESTJS)",
    "POSTGRESQL",
    "HEROKU",
    "FIREBASE",
  ];
  return (
    <div className="about">
      <div className="about__image-container">
        <img src={about_image} alt="" />
      </div>
      <div className="about__text-container">
        <div className="about__title-container">
          <h1 className="page-title">About Me</h1>
          <p className="page-subtitle">Why choose me?</p>
        </div>

        <div className="about__card">
          <p className="about__card-text">
            I am a systems engineer, focused on software development with
            technologies based on JAVASCRIPT, passionate about developing web
            and mobile applications, using agile methodologies and following
            clean code guidelines
          </p>

          <div className="about__card-subtext">
            <h2>My main stack for develop APPS</h2>

            {stacks.map((s) => (
              <div className="about__card-subtext-item" key={s}>
                <div></div>
                <p>{s}</p>
              </div>
            ))}
          </div>

          <div className="about__card-actions">
            <button
              className="btn btn-outline"
              onClick={() => navigate("/contact")}
            >
              Hire Me
            </button>
            <a className="btn btn-primary" href={cv} download="resume.pdf">
              Get resume
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};
