import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import { AiFillGithub, AiFillLinkedin } from "react-icons/ai";
import { useNavigate } from "react-router-dom";

import Swal from "sweetalert2";

import mail from "../assets/mail.svg";
import { fetchWithoutToken } from "../utils/fetch";

export const Contact = () => {
  const navigate = useNavigate();
  const onSubmit = async (values, { resetForm }) => {
    const { name: title, email, body } = values;
    resetForm();
    const resp = await fetchWithoutToken(
      "messages",
      { title, email, body },
      "POST"
    );

    await resp.json();

    resetForm();
    navigate("home", { replace: true });

    Swal.fire("Success", "Message sent", "success");
  };

  return (
    <div className="contact">
      <div className="contact__title-container">
        <h1 className="page-title">Contact</h1>
        <p className="page-subtitle">Let's stay in touch</p>
      </div>

      <div className="contact__card">
        <div className="contact__card-title">
          <h1>Get in Touch</h1>
          <div className="contact__card-social">
            <a
              className="social-link"
              href="https://github.com/troyanito123"
              target="_blank"
            >
              <AiFillGithub size={32} />
            </a>
            <a
              className="social-link"
              href="https://www.linkedin.com/in/jassmanni-quinones-alvarez"
              target="_blank"
            >
              <AiFillLinkedin size={32} />
            </a>
          </div>
        </div>

        <div className="contact__card-content">
          <div className="contact__card-image">
            <h2>Send your email here!</h2>
            <img src={mail} alt="mail" />
          </div>
          <Formik
            initialValues={{
              name: "",
              email: "",
              body: "",
            }}
            onSubmit={onSubmit}
            validationSchema={Yup.object({
              name: Yup.string().required().min(3),
              email: Yup.string().required().email(),
              body: Yup.string().min(3).max(244).required(),
            })}
          >
            {({ errors, touched }) => (
              <Form className="contact__card-form">
                <div className="contact__card-form-control">
                  <label htmlFor="name">Name:</label>
                  <Field name="name" type="text" />
                  <ErrorMessage
                    name="name"
                    component="span"
                    className="contact__card-form-error"
                  />
                </div>
                <div className="contact__card-form-control">
                  <label htmlFor="email">Email:</label>
                  <Field name="email" type="email" />
                  <ErrorMessage
                    name="email"
                    component="span"
                    className="contact__card-form-error"
                  />
                </div>

                <div className="contact__card-form-control contact__card-form-control-area">
                  <label htmlFor="body">Body:</label>
                  <Field name="body" type="text" as="textarea" />
                  <ErrorMessage
                    name="body"
                    component="span"
                    className="contact__card-form-error"
                  />
                </div>

                <button type="submit" className="btn btn-primary">
                  Submit
                </button>
              </Form>
            )}
          </Formik>
        </div>
      </div>
    </div>
  );
};
