import { useState } from "react";
import { NavLink } from "react-router-dom";

import { AiOutlineMenu } from "react-icons/ai";

export const Header = () => {
  const [toggle, setToggle] = useState(false);

  const onToggle = () => {
    setToggle((value) => !value);
  };

  const closeNavbar = () => {
    setToggle(setToggle(false));
  };

  return (
    <header className="navbar">
      <div className="header-toggle">
        <button onClick={onToggle}>
          <AiOutlineMenu size={50} color="#30aadd" />
        </button>
        <p>CLCI</p>
      </div>
      <ul className={!toggle ? "nav-links" : "nav-links nav-links-active"}>
        <li className="toggle">
          <button onClick={onToggle}>X</button>
        </li>
        <li className="">
          <NavLink
            to="home"
            className={({ isActive }) =>
              isActive ? "nav-item link-active" : "nav-item"
            }
            onClick={closeNavbar}
          >
            HOME
          </NavLink>
        </li>
        <li>
          <NavLink
            to="about"
            className={({ isActive }) =>
              isActive ? "nav-item link-active" : "nav-item"
            }
            onClick={closeNavbar}
          >
            ABOUT
          </NavLink>
        </li>
        <li>
          <NavLink
            to="contact"
            className={({ isActive }) =>
              isActive ? "nav-item link-active" : "nav-item"
            }
            onClick={closeNavbar}
          >
            CONTACT
          </NavLink>
        </li>
        <li>
          <NavLink
            to="proyects"
            className={({ isActive }) =>
              isActive ? "nav-item link-active" : "nav-item"
            }
            onClick={closeNavbar}
          >
            MY PROYECTS
          </NavLink>
        </li>
      </ul>
    </header>
  );
};
